.landing-ref-title {
  margin-top: 80px;

  color: #FFF;
  text-align: center;
  font-family: Open Sans;
  font-size: 56px;
  font-style: normal;
  font-weight: 800;
  line-height: normal;

  @media screen and (max-width: 768px) {
    font-size: 48px;
  }

  @media screen and (max-width: 425px) {
    margin-top: 48px;
    font-size: 24px;
  }
}