.landing-ref-socials {
  margin-top: 180px;

  @media screen and (max-width: 425px) {
    margin-top: 100px;
  }

  // Title
  &__title {
    background: linear-gradient(270deg, #8231C6 -5.7%, #FEAD37 45.44%, #FF555E 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;

    text-align: center;
    font-family: Open Sans;
    font-size: 49px;
    font-weight: 800;
    line-height: 140%;
    text-transform: uppercase;
  }

  @media screen and (max-width: 768px) {
    &__title {
      font-size: 36px;
    }
  }

  // Items
  &__items {
    margin-top: 80px;

    display: flex;
    justify-content: center;
  }

  &__item {
    flex: 1;

    width: 100%;
    max-width: 200px;
    margin-right: 24px;

    display: flex;
    flex-direction: column;
    align-items: center;

    text-decoration: none;

    &:last-child {
      margin-right: 0;
    }
  }

  &__item-image {
    position: relative;
    z-index: 0;

    width: 140px;
    height: 140px;

    &--telegram{
      background: url('../../../assets/landing-ref/images/tg.png') no-repeat center center / cover;
    }
    &--youtube{
        background: url('../../../assets/landing-ref/images/yt.png') no-repeat center center / cover;
    }
    &--vk{
        background: url('../../../assets/landing-ref/images/vk.png') no-repeat center center / cover;
    }
    &--instagram{
        background: url('../../../assets/landing-ref/images/insta.png') no-repeat center center / cover;
    }

    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);

      width: 200px;
      height: 200px;
      flex-shrink: 0;
      border-radius: 200px;
      background: linear-gradient(180deg, #98AEFF 0%, #7C46ED 70.14%);

      filter: blur(300px);
    }
  }

  &__item-text {
    position: relative;
    z-index: 1;

    color: #FFF;
    text-align: center;
    font-family: Open Sans;
    font-size: 32px;

    transition: 0.3s;
  }

  &__item:hover &__item-text {
    color: #8231C6;
  }

  @media screen and (max-width: 768px) {
    &__items {
      flex-wrap: wrap;
    }

    &__item {
      min-width: 50%;
      margin-right: 0;
      margin-bottom: 48px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  @media screen and (max-width: 425px) {
    &__items {
      flex-direction: column;
      align-items: center;
    }

    &__item {
      margin-right: 0;
      margin-bottom: 48px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}