.landing-ref-profile-info {
  margin-top: 160px;
  display: flex;

  @media screen and (max-width: 425px) {
    margin-top: 48px;
  }

  // Left
  &__left {
    flex: 1;
    position: relative;
    z-index: 1;

    display: flex;
    justify-content: flex-end;

    &::before {
      content: '';
      position: absolute;
      z-index: 0;
      top: 175px;
      right: 175px;
      transform: translate(50%, -50%);
      background: linear-gradient(180deg, #98AEFF 0%, #7C46ED 70.14%);
      width: 600px;
      height: 600px;
      border-radius: 600px;
      filter: blur(300px);
    }

    > * {
      position: relative;
      z-index: 1;
    }
  }

  @media screen and (max-width: 768px) {
    &__left {
      display: none;
    }
  }

  // Right
  &__right {
    flex: 1;
    position: relative;
    z-index: 1;

    margin-left: 64px;

    display: flex;
    flex-direction: column;
  }

  &__info {
    display: flex;
    align-items: center;
  }
  
  &__info-avatar {
    display: none;
    margin-right: 20px;
  }

  &__name {
    color: #FFF;
    font-family: Open Sans;
    font-size: 48px;
  }

  &__subtitle {
    color: #FFF;
    font-family: Open Sans;
    font-size: 20px;
  }

  &__about {
    flex: 1;

    background: rgba(255, 255, 255, 0.08);
    max-width: 560px;
    margin-top: 12px;
    border-radius: 26px;
    border: 2px solid rgba(255, 255, 255, 0.12);
    padding: 24px;

    color: #FFF;
    font-family: Open Sans;
    font-size: 24px;
  }

  &__phone {
    margin-top: 16px;
    padding-left: 8px;

    color: #FFF;
    font-family: Open Sans;
    font-size: 20px;
    font-weight: 400;
    line-height: 30px;

    span {
      text-wrap: nowrap;
      white-space: nowrap;
    }
  }

  &__prm {
    position: absolute;
    left: min(100%, 500px);
    bottom: 0;
    transform: translate(0, 80%);
  }

  &__blob-img {
    width: 140px;
    height: 140px;
  }

  &__made-by {
    margin-left: 50%;

    color: #FFF;
    font-family: Open Sans;
    font-size: 20px;
  }

  @media screen and (max-width: 1440px) {
    &__prm {
      right: 0;
      left: auto;
    }

    &__made-by {
      margin-left: 0;

      text-align: center;
    }
  }

  @media screen and (max-width: 768px) {
    &__right {
      margin-left: 0;
    }

    &__info-avatar {
      display: block;
    
      &::before {
        content: '';
        position: absolute;
        z-index: 0;
        top: 175px;
        right: 175px;
        transform: translate(50%, -50%);
        background: linear-gradient(180deg, #98AEFF 0%, #7C46ED 70.14%);
        width: 600px;
        height: 600px;
        border-radius: 600px;
        filter: blur(300px);
        opacity: 0.7;;
      }
    }

    &__about {
      max-width: 100%;
      margin-top: 20px;
    }
  }

  @media screen and (max-width: 425px) {
    &__name {
      font-size: 28px;
    }

    &__subtitle {
      font-size: 16px;
    }

    &__about {
      font-size: 20px;
    }

    &__prm {
      display: none;
    }
  }
}